import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";

import "assets/scss/material-kit-react.scss?v=1.8.0";

// pages for this product
import Components from "./views/Components/Components.js";
import ProfilePage from "./views/ProfilePage/ProfilePage.js";
import LoginPage from "./views/LoginPage/LoginPage.js";
import QuestionsPage from "./views/QuestionsPage/QuestionsPage.js";
import Header from "./components/Header/Header.js";
import HeaderLinks from "./components/Header/HeaderLinks.js";
import SubjectPage from "views/SubjectPage/SubjectPage.js";
import TopicPage from "views/TopicPage/TopicPage.js";
import { CookiesProvider } from "react-cookie";

var hist = createBrowserHistory();

ReactDOM.render(
  <CookiesProvider>
    <Router history={hist}>
      <Header
        brand="MIMIR"
        rightLinks={<HeaderLinks />}
        fixed
        color="white"
        changeColorOnScroll={{
          height: 50,
          color: "white"
        }}
      />
      <Switch>
        <Route path="/subjects" component={SubjectPage} />
        <Route path="/subject/:subject" component={TopicPage} />
        <Route path="/subject/:subject/:category" component={TopicPage} />
        <Route path="/topic/:id/:topic" component={QuestionsPage} />
        <Route path="/profile-page" component={ProfilePage} />
        <Route path="/login" component={LoginPage} />
        <Route path="/" component={Components} />
      </Switch>
    </Router>
  </CookiesProvider>,
  document.getElementById("root")
);
