import React, { useEffect, useState } from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";

// @material-ui/icons

// core components
import Footer from "components/Footer/Footer.js";

import styles from "assets/jss/material-kit-react/views/topicPage.js";
import mainStyles from "assets/jss/material-kit-react/views/components.js";
import { useHistory } from "react-router-dom";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem";
import {
  Paper,
  IconButton,
  InputBase,
  Button,
  List,
  ListItem,
  ListItemText,
  Divider
} from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import ListSection from "./Sections/ListSection";
import { CATEGORIES } from "constants/dummy";
import { TOPICS } from "constants/dummy";
import VideosList from "./Sections/VideosList";
import { VIDEOS } from "constants/dummy";

const dashboardRoutes = [];

const useStyles = makeStyles(styles);
const useMainStyles = makeStyles(mainStyles);
export default function TopicPage(props) {
  const classes = useStyles();
  const history = useHistory();
  const [input, setInput] = useState("");
  const { ...rest } = props;
  return (
    <div>
      <div className={classes.container}>
        <GridContainer>
          <GridItem xs={12} sm={12} md={8}>
            <h3>
              Get definitions of{" "}
              <span className={classes.topicHeader}>
                {history.location.pathname.split("/")[2].split("-")[1]}
              </span>
            </h3>
            <Paper component="form" className={classes.inputWrapper}>
              <IconButton className={classes.iconButton} aria-label="menu">
                <SearchIcon />
              </IconButton>
              <InputBase
                className={classes.searchInput}
                placeholder="Start typing you question"
                value={input}
                onChange={e => setInput(e.target.value)}
              />
              <Button color="transparent" className={classes.searchButton}>
                Search
              </Button>
            </Paper>
            {input.length > 0 && (
              <Paper component="form" className={classes.questionsWrapper}>
                <List className={classes.questionList}>
                  {[1, 2, 3, 4, 5, 6].map(item => (
                    <ListItem
                      alignItems="flex-start"
                      className={classes.questionList}
                    >
                      <div className={classes.question}>
                        Q: What is bandwidth and what is the type of cable that
                        has the highest bandwidth in any communication medium?
                      </div>
                      <div className={classes.answer}>See Answer</div>
                      <Divider variant="inset" component="li" />
                    </ListItem>
                  ))}
                </List>
              </Paper>
            )}
            {history.location.pathname.split("/").length === 3 && (
              <ListSection
                heading={
                  history.location.pathname.split("/")[2].split("-")[1] +
                  " definition by category"
                }
                list={CATEGORIES}
                onClick={(id, name) =>
                  history.push(
                    history.location.pathname.split("/")[2].split("-")[1] +
                      "/" +
                      id +
                      "-" +
                      name
                  )
                }
                type={"category"}
              />
            )}
            <ListSection
              heading={
                history.location.pathname.split("/").length > 3
                  ? decodeURIComponent(
                      history.location.pathname.split("/")[3].split("-")[1]
                    ) + " definitions"
                  : history.location.pathname.split("/")[2].split("-")[1] +
                    " definition by topic"
              }
              list={TOPICS}
              onClick={(id, name) => history.push("/topic/" + id + "/" + name)}
              type={"topic"}
            />
          </GridItem>
          <GridItem xs={12} sm={12} md={4}>
            <VideosList
              videos={VIDEOS}
              type={
                history.location.pathname.split("/").length === 3
                  ? "category"
                  : "topic"
              }
            />
          </GridItem>
        </GridContainer>
      </div>
      <Footer />
    </div>
  );
}
